<template>
    <div>
        <navbar title="意见反馈" v-if="$phonemodel == 'Android'"></navbar>
        <div class="app" :class="{nav:$phonemodel == 'Android'}">
            <form action>
                <label for="problem">问题和意见</label>
                <textarea id="problem" v-model="opinionForm.content" placeholder="具体说说您的问题，描述不少于20个字，我们会尽快为您解决。"></textarea>
                <label for="contact">您的联系方式</label>
                <input id="contact" type="email" v-model="opinionForm.email" placeholder="邮箱" />
            </form>
            <button class="verify" @click="submitOpinion">提交</button>
        </div>
    </div>
</template>

<script>
    import {option} from "../../api";
    import { toast, Dialog } from 'vant';
    import navbar from "@/components/navbar";
    import {isEmail} from "../../utils/validate";
    export default {
        name: "Opinion",
        components: {
            navbar
        },
        data()
        {
            return {
                opinionForm:{
                    email:'',
                    content:''
                }
            }
        },
        methods:{
            submitOpinion()
            {
                let form = this.opinionForm
                if (form.content == '' || form.content.length < 20)
                {
                    toast('反馈内容最少20个字符');
                    return;
                }
                if(form.email == '')
                {
                  toast('请输入邮箱');
                  return;
                }
                if(!isEmail(form.email))
                {
                    toast('请输入合法邮箱');
                    return;
                }
                option(form).then(res => {

                    Dialog.alert({
                        title: '系统提醒',
                        message: '意见反馈成功'
                    }).then(() => {

                        this.$router.go(-1)
                    });

                })
            }
        }
    }
</script>

<style scoped>
    @import "../../static/css/opinion.css";
</style>



